"use client";

import type React from "react";

import { useState, useEffect, useRef } from "react";
import { commands } from "@/lib/commands";

interface HistoryLine {
  type: "command" | "output" | "error";
  content: string | React.ReactNode;
  timestamp?: Date;
}

export default function Terminal() {
  const [input, setInput] = useState("");
  const [history, setHistory] = useState<HistoryLine[]>([]);
  const [commandHistory, setCommandHistory] = useState<string[]>([]);
  const [historyIndex, setHistoryIndex] = useState(-1);
  const [isTyping, setIsTyping] = useState(false);
  const inputRef = useRef<HTMLInputElement>(null);
  const terminalRef = useRef<HTMLDivElement>(null);
  const [suggestions, setSuggestions] = useState<string[]>([]);
  const [showModal, setShowModal] = useState(true);

  // Welcome message on mount
  useEffect(() => {
    const welcomeMessage = [
      { type: "output" as const, content: "Welcome to my terminal portfolio!" },
      { type: "output" as const, content: "" },
      {
        type: "output" as const,
        content: (
          <span>
            Type{" "}
            <span className="text-[#ef4444]">help</span> to see available
            commands.
          </span>
        ),
      },
      { type: "output" as const, content: "" },
    ];
    setHistory(welcomeMessage);
  }, []);

  // Auto-scroll to bottom
  useEffect(() => {
    if (terminalRef.current) {
      terminalRef.current.scrollTop = terminalRef.current.scrollHeight;
    }
  }, [history]);

  // Focus input when clicking anywhere in terminal
  const handleTerminalClick = () => {
    inputRef.current?.focus();
  };

  // Handle command execution
  const executeCommand = (cmd: string) => {
    const trimmedCmd = cmd.trim();

    if (!trimmedCmd) {
      setHistory((prev) => [...prev, { type: "command", content: "" }]);
      return;
    }

    // Add command to history
    setHistory((prev) => [...prev, { type: "command", content: trimmedCmd }]);
    setCommandHistory((prev) => [...prev, trimmedCmd]);
    setHistoryIndex(-1);

    // Parse command and args
    const [commandName, ...args] = trimmedCmd.split(" ");
    const command = commands[commandName.toLowerCase()];

    if (commandName.toLowerCase() === "clear") {
      setHistory([]);
      return;
    }

    if (command) {
      const output = command.execute(args);
      setHistory((prev) => [...prev, { type: "output", content: output }]);
    } else {
      setHistory((prev) => [
        ...prev,
        {
          type: "error",
          content: `Command not found: ${commandName}. Type "help" for available commands.`,
        },
      ]);
    }
  };

  // Handle input submission
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    executeCommand(input);
    setInput("");
    setSuggestions([]);
  };

  // Handle keyboard navigation
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    // Command history navigation
    if (e.key === "ArrowUp") {
      e.preventDefault();
      if (commandHistory.length > 0) {
        const newIndex =
          historyIndex === -1
            ? commandHistory.length - 1
            : Math.max(0, historyIndex - 1);
        setHistoryIndex(newIndex);
        setInput(commandHistory[newIndex]);
      }
    } else if (e.key === "ArrowDown") {
      e.preventDefault();
      if (historyIndex !== -1) {
        const newIndex = historyIndex + 1;
        if (newIndex >= commandHistory.length) {
          setHistoryIndex(-1);
          setInput("");
        } else {
          setHistoryIndex(newIndex);
          setInput(commandHistory[newIndex]);
        }
      }
    }
    // Tab completion
    else if (e.key === "Tab") {
      e.preventDefault();
      const availableCommands = Object.keys(commands);
      const matches = availableCommands.filter((cmd) =>
        cmd.startsWith(input.toLowerCase())
      );

      if (matches.length === 1) {
        setInput(matches[0]);
        setSuggestions([]);
      } else if (matches.length > 1) {
        setSuggestions(matches);
      }
    }
  };

  // Handle input change
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setInput(value);

    // Update suggestions
    if (value) {
      const availableCommands = Object.keys(commands);
      const matches = availableCommands.filter((cmd) =>
        cmd.startsWith(value.toLowerCase())
      );
      setSuggestions(matches.length > 1 ? matches : []);
    } else {
      setSuggestions([]);
    }
  };

  return (
    <>
      {/* Mobile: Bot√≥n para abrir modal */}

      {/* Modal en m√≥vil */}
      {showModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center  bg-black/20 backdrop-blur-sm animate-fadeIn md:hidden">
          <div className="bg-[#23272f] rounded-3xl p-8 shadow-2xl text-center max-w-xs w-full border border-[#22d3ee]/30 relative animate-modalPop">
            <span className="absolute -top-8 left-1/2 -translate-x-1/2 text-5xl select-none">
              üì±
            </span>
            <h2 className="text-2xl font-extrabold text-[#ef4444] mb-2 tracking-tight">
              Unsupported resolution
            </h2>
            <p className="text-[#e2e8f0] mb-4 text-base">
              This portfolio is optimized for large screens only.
              <br />
              That's why you can't interact with the terminal here.
            </p>
            <p className="text-[#94a3b8] mb-6 text-sm">
              You can visit my other portfolio designed for mobile:
            </p>
            <a
              href="https://portfolioboots.vercel.app/en" // <-- Change this URL to your other portfolio
              target="_blank"
              rel="noopener noreferrer"
              className="block px-5 py-3 bg-gradient-to-r from-[#22d3ee] to-[#22c55e] text-white rounded-xl font-bold text-lg mb-4 shadow-lg hover:from-[#0ea5e9] hover:to-[#16a34a] transition-all duration-200"
            >
              Go to my other portfolio
            </a>
          </div>
        </div>
      )}

      {/* Desktop: Terminal normal */}
      <div className="flex flex-col-reverse font-['JetBrains_Mono'] w-[960px] bg-andromedabackground rounded-lg drop-shadow-[0_12px_12px_rgba(255,255,255,0.15)]">
        {/* Terminal Window */}
        <div className="rounded-lg overflow-hidden shadow-sm   ">
          {/* Window Header */}
          <div className="bg-[#262a33] px-4 py-2 flex items-center gap-2">
            <div className="flex gap-2">
              <div className="w-3 h-3 rounded-full bg-[#ff5f56]" />
              <div className="w-3 h-3 rounded-full bg-[#ffbd2e]" />
              <div className="w-3 h-3 rounded-full bg-[#27c93f]" />
            </div>
          </div>

          {/* Terminal Content */}
          <div
            ref={terminalRef}
            onClick={handleTerminalClick}
            className="bg-[#262a33] p-4 h-[540px]  overflow-y-auto cursor-text scrollbar-thin scrollbar-thumb-[#475569] scrollbar-track-[#1e293b] scrollbar-thumb-rounded-md scrollbar-track-rounded-md"
            style={{
              scrollbarWidth: "thin",
              scrollbarColor: "#475569 #1e293b",
            }}
          >
            {/* History */}
            <div className="space-y-2">
              {history.map((line, index) => (
                <div key={index} className="terminal-line">
                  {line.type === "command" && (
                    <div className="flex gap-2">
                      <span className="select-none">
                        <span className="text-[#22c55e]">
                          BootsDev@BootsDev-X
                        </span>
                        <span className="text-white">:</span>

                        <span className="text-blue-600">~</span>
                        <span className="text-white">$</span>
                      </span>
                      <span className="text-[#e2e8f0]">{line.content}</span>
                    </div>
                  )}
                  {line.type === "output" && (
                    <div className="text-[#e2e8f0] whitespace-pre-wrap pl-0">
                      {line.content}
                    </div>
                  )}
                  {line.type === "error" && (
                    <div className="text-[#ef4444] pl-0">{line.content}</div>
                  )}
                </div>
              ))}
            </div>

            {/* Suggestions */}
            {suggestions.length > 0 && (
              <div className="mt-2 text-[#94a3b8] text-sm">
                {suggestions.join("  ")}
              </div>
            )}

            {/* Input Line */}
            <form onSubmit={handleSubmit} className="flex gap-2 mt-2">
              <span className="select-none">
                <span className="text-[#22c55e]">BootsDev@BootsDev-X</span>
                <span className="text-white">:</span>
                <span className="text-blue-600">~</span>
                <span className="text-white">$</span>
              </span>
              <input
                ref={inputRef}
                type="text"
                value={input}
                onChange={handleInputChange}
                onKeyDown={handleKeyDown}
                className="flex-1 bg-transparent text-[#e2e8f0] outline-none caret-[#22d3ee]"
                autoFocus
                spellCheck={false}
                autoComplete="off"
              />
            </form>
          </div>
        </div>

        {/* Hint Text (solo desktop) */}
        <div className="mt-4  mb-2 text-center text-gray-500 text-sm md:block hidden">
          <p>
            Press <kbd className="px-2 py-1 bg-gray-800 rounded text-xs">‚Üë</kbd>{" "}
            <kbd className="px-2 py-1 bg-gray-800 rounded text-xs">‚Üì</kbd> for
            command history ‚Ä¢{" "}
            <kbd className="px-2 py-1 bg-gray-800 rounded text-xs">Tab</kbd> for
            autocomplete
          </p>
        </div>
      </div>

      {/* Animaciones Tailwind personalizadas */}
      <style jsx>{`
        .animate-fadeIn {
          animation: fadeIn 0.3s ease;
        }
        @keyframes fadeIn {
          from {
            opacity: 0;
          }
          to {
            opacity: 1;
          }
        }
        .animate-modalPop {
          animation: modalPop 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55);
        }
        @keyframes modalPop {
          0% {
            transform: scale(0.7);
            opacity: 0;
          }
          80% {
            transform: scale(1.05);
            opacity: 1;
          }
          100% {
            transform: scale(1);
            opacity: 1;
          }
        }
      `}</style>
    </>
  );
}
